import React from 'react';
import './App.css';
import ReactForm from './Form';
import {BrowserRouter, Route, Switch} from 'react-router-dom';
import Terms from './terms';

function App() {
  return (
    <BrowserRouter>
    <div className="App">
<Switch>
<Route path="/terms" component={Terms} />
<Route path="/" component={ReactForm} />
</Switch>
    </div>
</BrowserRouter>
  );
}

export default App;
